.wl-video-modal {
  display: block; }
  .wl-video-modal__header {
    background: #FFFFFF;
    box-shadow: 0px 10px 27px rgba(0, 0, 0, 0.08);
    border-radius: 8px 8px 0px 0px;
    width: 100%;
    height: 10%;
    align-items: center; }
    .wl-video-modal__header__container {
      align-items: center; }
  .wl-video-modal--full-width {
    width: 90%;
    height: 80%;
    padding: 0; }
  .wl-video-modal__menu_button_container {
    justify-content: flex-end !important; }
  .wl-video-modal__menu_button {
    cursor: pointer; }
    .wl-video-modal__menu_button--disabled {
      cursor: not-allowed;
      opacity: 0.5; }
  .wl-video-modal__modal_body {
    width: 100%;
    height: 80%;
    padding: 0;
    overflow-y: scroll; }
